.footer {
    margin-top: 100px;
    background-color: #162e44;
    height: 50vh;
    color: #fff;
    display: flex;
    justify-content: center;
  }
  @media (max-width: 450px) {
    .footer {
      display: block;
      height: 100%;
    }
  }
  .footer__title1 {
    font-size: 24px;
    padding-bottom: 20px;
  }
  .footer__title2 {
    font-size: 24px;
    margin-bottom: 71px;
    margin-right: 100px;
  }
  @media (max-width: 450px) {
    .footer__title2 {
      margin-bottom: 0;
    }
  }
  .footer__title2 a {
    color: white;
  }
  .footer__title3 {
    margin-bottom: 71px;
    font-size: 24px;
  }
  @media (max-width: 450px) {
    .footer__title3 {
      margin-bottom: 0;
    }
  }
  .footer__text {
    font-size: 16px;
    display: flex;
    padding-top: 20px;
    transition: all 0.3s ease;
    cursor: pointer;
  }
  @media (max-width: 450px) {
    .footer__text {
      display: flex;
      width: 300px;
    }
  }
  .footer__text p {
    padding: 5px 20px;
  }
  @media (max-width: 450px) {
    .footer__text p {
      width: 200px;
    }
  }
  .footer ul {
    width: 350px;
  }
  @media (max-width: 450px) {
    .footer ul {
      padding: 20px 0px;
    }
  }
  .footer div {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 200px;
  }
  @media (max-width: 450px) {
    .footer div {
      margin-left: 0;
      display: block;
      padding: 30px 20px;
    }
  }
  .footer__nav {
    color: white;
    font-size: 16px;
    transition: all 0.3s ease;
    padding-top: 17px;
  }
  .footer__nav:hover {
    color: #01b3a7;
  }
  .footer__text:hover {
    color: #01b3a7;
  }
  .footer__text1 {
    padding-top: 0;
  }
  
  