}
#details {
  display: none;
}
.attraction {
  cursor: pointer;
  margin: 10px 0;
}
#map {
  width: 100%;
  height: 400px;
}
.info__main {
  display: flex;
}
@media (max-width: 450px) {
  .info__main {
    display: block;
    margin-top: 50px;
  }
}
.info h2 {
  color: #000000;
}
.info__img {
