  }
}
.info__btn {
  padding: 30px 50px;
  font-size: 30px;
  margin-top: 50px;
  border: 0;
  background: #59cab0;
  color: white;
  transition: all 0.9s cubic-bezier(0.19, 1, 0.22, 1);
  box-shadow: inset 0 0 20px rgba(255, 255, 255, 0);
  cursor: pointer;
}
@media (max-width: 450px) {
  .info__btn {
    padding: 10px 30px;
  }
}
.info__btn a {
  color: #000000;
  
}
.info__btn:hover {
  border: 4px solid;
  box-shadow: inset 0 0 20px rgba(255, 255, 255, 0.5), 0 0 20px rgba(255, 255, 255, 0.2);
  outline-color: rgba(255, 255, 255, 0);
  outline-offset: 15px;
  text-shadow: 1px 1px 2px #427388;
  background-color: white;
  color: #000000;
}
.info__mini-box {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 450px) {
  .info__mini-box {
    display: block;
  }
}
#categoryFilter{
  background-color: #01b3a7;
  padding: 15px 25px;
  font-size: 16px;
  border: 0px;
  border-radius: 10px;
  cursor: pointer;
  margin-left: 10px;
  @media (max-width: 450px) {
    margin: 20px 0 0 70px;
  }
}
.modal__reviews {
  width: 95%; 
  margin-left: 20px;
  padding: 10px;
  border-left: 5px solid #ccc; 
  box-sizing: border-box;
}

.modal__reviews h3 {
  font-size: 36px;
  margin-bottom: 10px;
}

.reviews-list {
  max-height: 300px; 
  overflow-y: auto; /* Прокрутка, если отзывов много */
}

.review {
  margin-bottom: 15px;
  padding: 10px;
  border: 3px solid #ddd;
  border-radius: 5px;
  background-color: #f9f9f9;
}

.review__author {
  font-weight: bold;
  margin-bottom: 5px;
}

.review__text {
  font-size: 24px;
}
.review-form {
  margin-top: 20px;
}

.review-form input,
.review-form textarea {
  font-size: 24px;
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 3px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box;
}

.modal__base{
  display: flex;
}
.review__delete {
  margin-top: 10px;
  padding: 5px 10px;
  background-color: #ff4d4d;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.review__delete:hover {
  background-color: #cc0000;
}
.rating__star{
  font-size: 24px;
  cursor: pointer;
}
.container button {
  color: #000;
  padding: 40px 100px;
  border-radius: 24px;
  background-color: #fff;
  font-size: 36px;
  transition: all 0.5s;
}
@media (max-width: 450px) {
  .container button {
    padding: 20px 20px;
  }
}
.container button:hover {
  background-color: #01b3a7;
  border: 0px;
  color: #fff;
}

.btn {
	background-color: #2A265F;
	border: 0;
	border-radius: 50px;
	box-shadow: 0 10px 10px rgba(0, 0, 0, 0.2);
	color: #fff;
	font-size: 16px;
	padding: 12px 25px;
	position: absolute;
	bottom: 30px;
	right: 30px;
	letter-spacing: 1px;
}
.social-panel-container {
  height: 375px;
  z-index: 1;
	position: fixed;
	right: 0;
	bottom: 565px;
	transform: translateX(100%);
	transition: transform 0.4s ease-in-out;
  @media (max-width:450px) {
    left: 20px;
    bottom: 275px;
  }
}

.social-panel-container.visible {
	transform: translateX(-10px);
}

.social-panel {	
	background-color: #fff;
	border-radius: 16px;
	box-shadow: 0 16px 31px -17px rgba(0,31,97,0.6);
	border: 5px solid #001F61;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	font-family: 'Muli';
	position: relative;
	height: 900px;	
	width: 1860px;
	max-width: calc(100% - 10px);
  @media(max-width:450px) {
    height: 625px;
  }
}

.social-panel button.close-btn {
  padding: 5px 10px;
	border: 0;
	color: #97A5CE;
	cursor: pointer;
	font-size: 20px;
	position: absolute;
	top: 5px;
	right: 5px;
}


.main__modal {
  width: 100%;
}
.modal__img{
  width: 450px;
  height: 300px;
}
.modal__box{
  display: flex;
  padding: 0px 35px;
  @media (max-width:450px) {
    display: block;
  }
}
.modal__title{
  font-size: 36px;
  text-align: center;
  padding-bottom: 20px;
