  }
}
#map{
  width: 975px;
  padding-top: 25px;
  height: 400px;
  @media (max-width:450px) {
   padding-top: 10px;
   height: 240px;
   width: 345px;
  }
}
.modal__text{
  width: 750px;
  font-size: 24px;
  @media (max-width:450px) {
    font-size: 12px;
