#pagination button{
  width: 40px;
  font-size: 20px;
  margin-left: 10px;
  border-radius: 7px;
  padding: 10px;
  background-color: #179cd1;
  color: #fff;
  border: 0px;
  transition: ease 0.2s;
  margin-top: 20px;
}
#pagination button:hover{

  width: 60px;
  background-color: #48ced379;
  color: #3d5c70;
  cursor: pointer;
}
#pagination{
  justify-content: center;
  display: flex;

  @media (max-width:450px) {
    padding-top: 20px;
    display: block;
  }
}

.loader {
  position: fixed;
  top: 40%; 
  left: 50%; 
  width: 48px;
  height: 48px;
  border-radius: 50%;
  display: inline-block;
  border-top: 5px solid #000000;
  border-right: 3px solid transparent;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
} 
 

.sortName{
  background-color: #01b3a7;
  padding: 15px 25px;
  font-size: 16px;
  border: 0px;
  border-radius: 10px;
  transition: ease 0.4s;
  margin-left: 200px;
  cursor: pointer;
  @media (max-width:450px) {
    margin-left: 70px;
  }
}
.sortName:hover{
  background-color: #fff;
  color: #179cd1;
}
.fullscreen-gallery {
  
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 90vh;
  background: rgba(0, 0, 0, 0.9);
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.fullscreen-gallery__content {
  max-width: 90%;
  max-height: 90%;
}

.fullscreen-gallery__image {
  box-sizing: content-box;
  width: 800px;
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.fullscreen-gallery__close,
.fullscreen-gallery__prev,
.fullscreen-gallery__next {
  position: absolute;
  color: white;
  font-size: 30px;
  cursor: pointer;
}

.fullscreen-gallery__close {
  top: 20px;
  right: 20px;
}

.fullscreen-gallery__prev {
  left: 20px;
  top: 50%;
  transform: translateY(-50%);
}

.fullscreen-gallery__next {
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
}
.modal__gallery-item{
  cursor: pointer;
  margin: 20px 0px 0px 35px;
  width: 200px;
  height: 150px;
  @media (max-width:450px) {
    width: 100px;
    height: 100px;
    margin-left: 10px;
  }
}

  .no-reviews {
    text-align: center;
    color: #888;
    font-style: italic;
    margin-top: 20px;
}
.fullscreen-gallery {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.9);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.fullscreen-image {
    max-width: 90%;
    max-height: 90%;
    object-fit: contain;
}

.close-fullscreen {
    position: absolute;
    top: 20px;
    right: 20px;
    background: none;
    border: none;
    color: white;
    font-size: 24px;
    cursor: pointer;
}

.gallery-prev-fullscreen,
.gallery-next-fullscreen {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background: none;
    border: none;
    color: white;
    font-size: 24px;
    cursor: pointer;
}

.gallery-prev-fullscreen {
    left: 20px;
}

.gallery-next-fullscreen {
    right: 20px;
}
