.about {
    display: flex;
    justify-content: space-between;
    padding: 0 150px;
  }
  
  @media (max-width: 450px) {
    .about {
      padding: 0;
      margin: auto 12%;
      display: block;
    }
  }
  
  .about__main {
    border: 1px solid #e1e1e1;
    width: 230px;
    padding: 100px;
    margin-top: 100px;
    transition: all 0.5s;
  }
  
  .about__main div {
    text-align: center;
  }
  
  .about__main div div p {
    font-size: 24px;
    transition: all 0.5s;
    cursor: pointer;
  }
  
  .about__main div div p:hover {
    color: #01b3a7;
  }
  
  @media (max-width: 450px) {
    .about__main {
      padding: 50px;
      margin-top: 25px;
    }
  }
  
  .about__main:hover {
    box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
  }